header {
  height: 100vh;
  width: auto;
  background-image: linear-gradient(#0006, #000), url("1895Conservatoire.1d66fe89.jpg");
  background-position: top;
  background-size: cover;
  background-attachment: fixed;
}

.one-third {
  height: 50vh;
}

.featured {
  width: 90%;
  position: absolute;
  bottom: 5%;
  left: 5%;
}

#featured-span {
  color: var(--secondary-light-color);
  font-size: 5rem;
  font-style: italic;
  font-weight: 300;
  display: block;
}

.featured :not(h1) {
  color: var(--secondary-light-color);
}

.benefits li {
  margin-left: 3rem;
  list-style: disc;
}

.horn-list a {
  font-size: 1.8rem;
}

.horn-list a:hover {
  color: var(--secondary-color);
}

.portrait {
  height: 50vh;
  min-height: 20rem;
}

.dedicatee li {
  margin-left: 2rem;
  list-style: inside;
}

.dedicatee li a {
  font-size: 1.8rem;
}

.more li {
  margin-left: 2rem;
}

.excerpt {
  font-size: 1.8rem;
}

.items {
  grid-template-columns: repeat(3, 1fr);
  margin: 5rem 0;
  display: grid;
  box-shadow: 0 .5rem 1rem #0000004d;
}

.item {
  background: var(--primary-color);
  position: relative;
  overflow: hidden;
}

.item:after {
  content: "";
  background: inherit;
  opacity: .9;
  width: 100%;
  height: 100%;
  transition: transform 3s cubic-bezier(.2, 1, .3, 1);
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transform: scale(2)translateX(-75%)translateY(-75%)rotate(-28deg);
}

.item:hover:after {
  transform: scale(2)translateX(0)translateY(0)rotate(-28deg);
}

.item:hover .item-image {
  transform: scale(1.05);
}

.item:hover .item-text {
  opacity: 1;
  transform: translateY(0);
}

.item-image {
  height: auto;
  transition: transform .75s cubic-bezier(.2, 1, .3, 1);
  display: block;
  transform: translateZ(0);
}

.item-image:before {
  content: "";
  padding-top: 75%;
  display: block;
  overflow: hidden;
}

.item-image img {
  width: 100%;
  height: auto;
  line-height: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.item-text {
  opacity: 0;
  text-align: center;
  z-index: 1;
  color: #fff;
  transition: opacity .5s cubic-bezier(.2, 1, .3, 1) .3s, transform .5s cubic-bezier(.2, 1, .3, 1) .3s;
  position: absolute;
  inset: 0;
  transform: translateY(-20%);
}

.item-text-wrap {
  width: 100%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.item-text-title {
  margin: .5rem 0 0;
  padding: 0 1rem;
  font-size: 2rem;
}

.item-text-category {
  text-transform: uppercase;
  opacity: .7;
  margin: 0;
  font-size: 2rem;
}

.instructions {
  font-style: italic;
}

.copyrights a {
  font-size: 1.8rem;
}

.copyrights .licence a {
  font-size: 1.5rem;
}

.licence {
  margin: 1rem 0 1rem 2rem;
  font-size: 1.5rem;
  display: block;
}

